import "./style.css";
import { useState } from "react";
import React from "react";
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContentText from '@material-ui/core/DialogContentText';
import Dialog from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';

function Header(props)
{
    // const [cartnum, setcartnum] = useState(0);
  //  console.log('===> ', props?.value || null)
    // const handleInput = ()=>
    //  {
    //      alert(props.value);
    //      setcartnum(prevState => prevState + props.value);
    // }
    const [open, setOpen] = React.useState(false);
    const [Input2, setInput2] = React.useState(1);
  
  const handleClickOpen = () => 
  {
    setOpen(true);       
   };
  
  const handleClose = () => {
    setOpen(false);
  };
  
   
    
    return (
        <div className= "head">
        <h3>Desi Food Restaurant</h3>
        <div  className ="Cart">
             <button onClick= {handleClickOpen}>Your Cart { props?.value || 0 }</button> 
            
            

            </div> 
      <Dialog open={open} onClose={handleClose}>
      <DialogTitle>
          Your Order
        </DialogTitle>
        
        {
        props.cartdata.map((item , index)=>{
        return(
            <>
            <DialogContent>
          <DialogContentText>
              Your Order:   
           {item.Amount * Input2}
           <input 
            type="number" 
            id="points" 
            name="points" 
            step="1" 
            min = "0" 
            onChange={(e) => {
            setInput2(parseInt(e.target.value));            
            }} ></input>
           
        
            </DialogContentText>
          </DialogContent>
            </>

        )})
        

        }
        
            
          
        <DialogActions>
          <Button onClick={handleClose} color="primary">
           Close
          </Button>
          <Button onClick={handleClose} color="primary" autoFocus>
           Order
          </Button>
        </DialogActions>
      </Dialog>
        

            </div>
            
     
)
}
export default Header;