import "./style.css";
import Images from './Images'
import React , { useState } from "react";
import Header from "./Header";

function Card(props)
{    
     const[Input1, setInput1] = useState(1);
     const [Amount, setAmount] = useState(props.Amount);

    //  const handleInput1 = (e)=>
    //  {
    //     setInput(e.target.value);
    //   }
    console.log(Input1);
     function handleInput()
     {
        setAmount(Amount * Input1);
        props?.onSaveToCart(Input1) ;// from here value  is sending to the header
      }
    return (
        
        <div className="cards">
        <div className= "sidebar"></div>
           <Images  imgsrc ={props.imgsrc}/> 
          <span className="titlefun">{props.name}</span>
          <p>Amount : <strong>{Amount}</strong></p>
          <input 
            type="number" 
            id="points" 
            name="points" 
            step="1" 
            min = "0" 
            onChange={(e) => {
            setInput1(parseInt(e.target.value));            
            }} ></input>
          <button className ="buttonCart" onClick={handleInput}>{props.button}</button>  
          <p>{props.description}</p>
           
        </div>

    )
}
export default Card;